.container {
  display: flex;
  flex-direction: column;
  padding: 0 0.5rem;
  font-family: 'Montserrat', sans-serif;
  font-size: 0.85rem;

  .name {
    font-weight: 700;
  }

  .gain {

    .iconPositive {
      margin-left: 0.5rem;
      margin-top: -0.2rem;
    }

    .iconNegative {
      margin-left: 0.5rem;
      margin-top: 0;
    }

    .positive {
      color: #00CF7E;
      font-weight: 700;
    }

    .negative {
      color: #E92017;
      font-weight: 700;
    }
  }

  .action {
    margin: auto;
    //margin-left: -1.5rem;
    font-weight: 500;
    color: #e81c1c;
  }

  .action.processing {
    color: #00CF7E;
  }

  .action.inactive {
    color: #878787;
  }

  .pagination {
    display: flex;
    justify-content: center;
    align-items: center;

    .left {
      color: #42287D;
      font-size: 1.2rem;
      margin-right: 0.8rem;
    }

    .center {
      display: flex;
      justify-content: center;
      align-items: center;

      .pageItem {
        font-weight: 700;
        width: 1.6rem;
        height: 1.6rem;
        line-height: 1.6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 100%;
        color: #42287D;
      }

      .pageItem.active {
        color: white;
        background-color: #42287D;
      }
    }


    .right {
      color: #42287D;
      font-size: 1.2rem;
      margin-left: 0.8rem;
    }

  }
}

.confirm {
  padding: 1rem 0;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-size: 1.2rem;

  .email {
    font-weight: 700;
  }
}

.header {
  .text {
    font-weight: 700;
    font-size: 0.7rem;
    border-left: 2px solid #b7b7b7;
    padding-left: 0.5rem;
    color: #878787;
  }
}

.usersTableHeader {
  font-family: 'Montserrat', sans-serif;
  color: #898989;
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.subTableHeader {
  font-family: 'Montserrat', sans-serif;
  color: #898989;
  padding: 0.5rem;
  font-size: 1.2rem;
  font-weight: 500;
}

.tablesHat {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;

  .tablesHatTab {
    color: #878787;
    font-size: 1rem;
    font-weight: 700;
    padding: 0 0.75rem;
    margin-right: 0.5rem;
    //border: 1px solid rgba(135, 135, 135, 0.21);
    border-radius: 0.2rem;
  }

  .tablesHatTabActive {
    //background-color: #42287D;
    background-color: #3B509F;
    color: white;
    //border: none;
  }

  .sum {
    color: #878787;
    font-size: 1rem;
    font-weight: 700;
    margin-left: 0.5rem;

    .num {
      font-weight: 700;
    }
  }
}
