.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
}

.itemsContainer {
  width: 100%;
  padding-bottom: 1rem;
}

.warning {
  padding: 0 0.5rem;
  font-family: 'Montserrat', sans-serif;
  color: #878787;
  font-weight: 300;
  font-size: 0.9rem;
  justify-self: flex-end;
}

.itemContainer {
  padding: 0.7rem;
}

.item {
  -webkit-box-shadow: 2px 2px 2px 0px rgba(0,0,0,0.15);
  -moz-box-shadow: 2px 2px 2px 0px rgba(0,0,0,0.15);
  box-shadow: 2px 2px 2px 0px rgba(0,0,0,0.15);

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: white;
  padding: 2rem 1.5rem;
  border-radius: 0.2rem;
  min-width: 100%;

  .main {
    .header {
      font-family: 'Akrobat Bold', sans-serif;
      display: flex;
      flex-direction: column;
      //align-items: center;
      justify-content: flex-start;
      margin-bottom: 0.25rem;

      .headerIconContainer {
        display: flex;
        padding-right: 10px;
        .headerIconImage {
          width: 35px;
          height: 35px;
        }
      }

      .headerText {
        display: flex;
        align-items: center;
        .title {
          font-size: 1.5rem;
          margin-right: 1rem;
          line-height: 1.1
        }
      }

      .percents {
        font-size: 1rem;
        white-space: nowrap;
        //padding-left: 50px;
        .upIcon {
          width: 16px;
          margin: -0.5rem 0.5rem 0 0;
        }
      }

    }

    .about {
      font-family: 'Roboto', sans-serif;
      line-height: 1.2rem;
      font-size: 0.95rem;
      font-weight: 300;
      //
      white-space: pre-line;
      //
      //text-align: justify;
      padding: 0.25rem 0;
    }
  }

  .bottom {
    margin-top: 0.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;

    .minimumPrice {
      font-family: 'Akrobat Bold', sans-serif;
      font-weight: 300;
      font-size: 1.1rem;
      line-height: 1.1;
    }

    .comment {
      font-family: 'Roboto', sans-serif;
      font-size: 0.8rem;
    }

    .buttonContainer {
      margin-top: 0.2rem;
      height: 2.2rem;

      .button {
        padding: 0 1.5rem;
        font-size: 0.75rem;
      }
    }
  }

}

.modal {
  background-color: #42287D;
  border-radius: 0.2rem;
  padding: 1rem 2rem;
  flex-direction: column;
  color: white;
  position: relative;

  .title {
    font-family: 'Akrobat Bold', sans-serif;
    font-size: 1.6rem;
  }

  .slider {
  }

  .sliderInfo {
    font-family: 'Akrobat Bold', sans-serif;
    font-size: 1.2rem;
    display: flex;
    justify-content: space-between;
  }

  .switch {
    margin-left: -12px;
    font-family: 'Montserrat', sans-serif;
    font-size: 1.1rem;
  }

  .buttonContainer {
    .button {

    }

    margin-top: 0.8rem;
  }

  .sum {
    font-family: 'Montserrat', sans-serif;
    font-size: 1.3rem;
    margin-top: 1rem;
    margin-bottom: 0.2rem;
    display: flex;
    align-items: center;
  }

  .sum span {
    font-weight: 700;
  }

  .confirm {
    padding: 1rem 0;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-size: 1.2rem;

    .email {
      font-weight: 700;
    }
  }

  .times {
    position: absolute;
    top: 0;
    right: 0;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    width: 30px;
    height: 30px;
    transform: translate(25%, -25%);
    color: black;
    font-size: 20px;
  }
}
