.header {
  background-image: url("images/bg.png");
}

.brief {
  font-family: "Open Sans", sans-serif;
  padding: 2rem 1rem;
  text-align: justify;
  margin: auto;
}

.iconItemsContainer {
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: #f7f7f7;
  padding: 2rem 0;
  margin: auto;
}

.numberItemsContainer {
  padding-bottom: 2rem;
  margin: auto;
}

.iconItem {
  padding: 0.5rem;
  flex-direction: column;
  align-items: center;

  .icon {
    width: 8rem;
  }

  .text {
    font-family: "Open Sans", sans-serif;
    text-align: center;
  }
}

.numberItem {
  padding: 0.5rem;
  position: relative;
  min-height: 10rem;
  align-items: center;

  .number {
    position: absolute;
    top: 0.5rem;
    left: 0.5rem;
    font-weight: 900;
    font-size: 10rem;
    line-height: 1;
    color: #ededed;
    z-index: 0;
  }

  .text {
    font-family: "Open Sans", sans-serif;
    font-size: 0.9rem;
    margin-top: 2rem;
    margin-left: 3rem;
    z-index: 1;
  }
}
