.container {
  padding: 1rem;
}

.tableContainer {
  margin-top: 1rem;
}

.content {
  font-family: 'Montserrat', sans-serif;
  //width: 600px;

  .title, .subtitle {
    margin-bottom: 1rem;
  }

  .item {
    margin-bottom: 1rem;
    background-color: white;
    height: 3.5rem;
    font-size: 1.1rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    cursor: pointer;

    -webkit-box-shadow: 0px 10px 12px -12px rgba(0,0,0,0.6);
    -moz-box-shadow: 0px 10px 12px -12px rgba(0,0,0,0.6);
    box-shadow: 0px 10px 12px -12px rgba(0,0,0,0.6);
    transition: 0.15s ease-in-out;

    .name {
      font-weight: 700;
    }

    .hash {

    }
  }

  .item.active {
    background-color: #00cf7e;
    cursor: default;
    .hash {
      cursor: text;
    }
  }

  .deposit {
    margin-bottom: 1rem;
  }

  .deposit, .withdraw {
    height: 3rem;
    display: flex;
    //justify-content: space-between;
    align-items: center;
  }

  .button {
    height: 100%;
    margin-left: 0.5rem;
    //width: 65%;
    width: 10rem;
    border-radius: 0.25rem;
    font-weight: 700;
    background-color: #2157a1;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.9rem;
  }
}

.modal {
  text-align: center;
  padding: 1rem;
  font-family: 'Montserrat', sans-serif;
  font-size: 1.2rem;

  .email {
    font-weight: 700;
  }
}

.depositModal {
  font-family: 'Montserrat', sans-serif;
  font-size: 1.3rem;
  padding: 1rem 1rem;
  overflow: auto;
  flex: 1;

  .modalHeader {
    margin-bottom: 0.2rem;
  }

  .cardField {
    font-size: 1.35rem;
    font-weight: 500;
    width: 90%;
    height: 40px;
    border-radius: 0.2rem;
    overflow: hidden;
    margin-bottom: 0.5rem;
    background-color: #FAFAFA !important;
    border: none;
    outline: none;
    padding: 0 1rem;
  }

  .amountField {
    width: 160px;
    height: 40px;
    border-radius: 0.2rem;
    overflow: hidden;
    margin-bottom: 0.5rem;
    background-color: #FAFAFA;
  }

  .depositModalButton {
    margin-top: 0.5rem;
  }

  .transactionPageButton {
    margin-bottom: 1rem;
  }

  .transactionHeader {
    font-weight: bold;
    font-size: 1.5rem;
  }

  .transactionSum {
    margin-bottom: 1rem;
  }

  .fail {
    font-family: 'Montserrat', sans-serif;
    margin-top: 0;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 1rem;
    text-align: center;
    opacity: 0;
    height: 0;
    transition: 200ms;
  }

  .failActive {
    margin-top: 0.5rem;
    height: unset;
    opacity: 1;
  }
}

.modalWrapper {
  font-family: 'Montserrat', sans-serif;
  padding: 0;
}

.modalHat {
  padding: 1rem 2rem;
  font-size: 1.7rem;
  font-weight: 700;
  //border-bottom: 3px solid rgba(255, 255, 255, 0.3);
  border-bottom: 3px solid #1ac0ea;
}

.modalMain {
  display: flex;
}

.bank {
  font-size: 1rem;
  font-family: 'Montserrat', sans-serif;

  font-weight: 500;
  .bankControl {
    display: flex;
    font-weight: 700;
    height: 40px;
    div {
    }
    margin-bottom: 0.5rem;
  }
  .toAcc {
    padding: 0.3rem 0;
    font-size: 1.3rem;
    display: flex;
    align-items: center;
    border-width: 2px 0;
    border-style: solid;
    border-color: rgba(255, 255, 255, 0.3);
    margin: 0.3rem 0;
    div {
      flex: 1;
    }
  }

  ul {
    li {
      a {
        color: white;
        text-decoration: underline;
        font-weight: 300;
      }
    }
  }

}

.modalMenu {
  width: 17rem;
  border-right: 1px solid rgba(255, 255, 255, 0.2);

  .menuIcon {
    width: 3.2rem;
    justify-content: center;
    align-items: center;
    display: flex;
    color: white;
    font-size: 1.3rem;
  }

  .modalTab {
    padding: 0.5rem 0.5rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    font-size: 1.1rem;
    display: flex;
    align-items: center;
  }

  .modalTab.active {
    background-color: #2d4293;
  }
}

.tableConfirm {
  color: #E92017;
}

.box {
  width: 10rem;
  height: 100%;
  margin-right: 0.5rem;

  -webkit-box-shadow: 0px 10px 12px -12px rgba(0,0,0,0.6);
  -moz-box-shadow: 0px 10px 12px -12px rgba(0,0,0,0.6);
  box-shadow: 0px 10px 12px -12px rgba(0,0,0,0.6);

  .input {
    border: none;
    border-radius: 0.25rem;
    font-size: 1.35rem;
    font-weight: 500;
    height: 100%;
    width: 100%;
    outline: none;
    padding: 0 1rem;
  }
}
