.table {
  border: none;

  .rt-table {
    border: none;
    overflow: visible !important;
  }

  .rt-thead {
    background-color: #f2f2f2;
    margin-bottom: 0.2rem;
    height: 2rem;

    .rt-tr {
      height: inherit;
    }
  }

  .rt-th {
    height: 2rem;
    border: none !important;
    display: flex;
    align-items: center;
    padding: 0 0 0 1rem !important;
  }

  .rt-tbody {
    border: none;
    overflow: visible !important;
  }

  .rt-tr-group {
    border: none;
    margin: 0.2rem 0;
    //background-color: white;
  }

  .rt-tr {
    border: none;
    height: 55px;
    font-weight: 500;
    -webkit-box-shadow: 0px 10px 12px -12px rgba(0,0,0,0.6);
    -moz-box-shadow: 0px 10px 12px -12px rgba(0,0,0,0.6);
    box-shadow: 0px 10px 12px -12px rgba(0,0,0,0.6);
    background-color: white;
  }

  .rt-td {
    border: none !important;
    display: flex;
    align-items: center;
    padding: 0 1.5rem;
  }

  .rt-expandable {
    padding: 0.5rem;
  }

  .pagination-bottom {
    margin: 1rem 0;
    position: relative;
  }
}

.table.small {
  font-size: 0.9rem !important;
  margin-bottom: 0;
  .rt-tr {
    height: 40px;
    background-color: transparent;
  }
  .rt-tr-group {
    background-color: white;
  }
}
