.container {
  //position: absolute;
  display: flex;
  height: 3.3rem !important;
  min-height: 3.3rem !important;
  font-family: 'Montserrat', sans-serif;
  //width: 100%;
  border-bottom: 1px solid #E4E4E4;
  padding: 0 1rem;
}

.user {
  //flex: 1;
  flex: 0;
  display: flex;
  align-items: center;
  font-size: 1.2rem;
  //padding: 0 2rem;

  .id {
    white-space: nowrap;
    .text {
      font-weight: 700;
    }
    padding-right: 1rem;
    //border-right: 2px solid #333;
  }

  .name {
    line-height: 1.1;
    padding-left: 1rem;
  }
}

.button {
  flex: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 12rem;
  text-align: center;
  height: 2.5rem;
  border-radius: 1.25rem;
  margin: 0.5rem;
  border: 2px solid #2157a1;
  //background-color: #2c5bca;
  font-weight: 700;
  font-size: 0.8rem;
  //color: white;
}

.balance {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  //justify-content: center;
  //background-color: #2bc0e8;
  //min-width: 15rem;
  padding: 0 1rem;

  .text {
    font-weight: 700;
  }
}
