.header {
  background-image: url("images/bg.png");
}

.container {
  font-family: "Open Sans", sans-serif;
  flex-direction: column;
  padding-bottom: 2rem;
  margin: auto;
}

.advantageList {
  text-align: left;
  padding: 1rem 0;
}

.subheader {
  //padding: 1rem 0;
  padding-bottom: 1rem;
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  font-size: 1.8rem;
  text-align: center;
}

.advantageItem {
  display: flex;
  align-items: center;
  padding: 0.5rem 0;

  .icon {
    width: 1.8rem;
  }

  .text {
    font-size: 0.9rem;
  }
}

.pollList {
  padding: 1rem 0;
  font-weight: bold;
}

.pollItem {
  display: flex;
  align-items: center;
  padding: 0.5rem 0;

  .number {
    font-family: "Raleway", sans-serif;
    font-weight: 800;
    font-size: 4rem;
    padding-right: 1rem;
    color: #c9c9c9;
    line-height: 1;
  }

  .text {
    font-size: 0.9rem;
  }
}
