$height: 100vh;
//$height: calc(100vh - 44px);

.container {
  position: relative;
  min-height: $height;
  width: 100%;
  margin-bottom: 44px;
}

.container > div {
  height: $height;
}

.slide {
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover;
  height: $height;
  color: white;
  padding: 0 2rem;

  .title {
    font-family: 'Raleway', sans-serif;
    font-size: 2rem;
    font-weight: 800;
  }

  .text {
    font-family: 'Open Sans', sans-serif;
    font-size: 1.6rem;
    font-weight: 300;
    max-width: 600px;
    //margin-top: 0.5rem;
    margin-bottom: 1rem;
  }
}

