.holder {
  //background-color: rgba(247,252,255,0.15);
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
  padding: 1rem 0;

  .container {
    align-items: center;
  }

  .logo {
    width: 16rem;
  }

  .tabs {
    width: unset;
    display: flex;

    .tab {
      padding: 0 0.5rem;
      white-space: nowrap;
      color: white;
      font-family: "Raleway", sans-serif;
      font-weight: 600;
      font-size: 0.9rem;
      //transition: 0.25s ease-in-out;
    }

    .tab.active {
      color: #1ac0ea;
    }
  }
}
