.container {
  background-color: #f8f8f8;
  width: 100%;
  display: flex;
  justify-content: center;
}

.address {
  flex-direction: column;
  padding: 2rem;

  .info {
    display: flex;
    align-items: center;
    font-family: "Open Sans", sans-serif;
    font-size: 1.1rem;

    img {
      width: 2.5rem;
    }
  }

  .title {
    font-family: "Akrobat Bold", sans-serif;
    font-weight: 800;
    font-size: 2rem;
    color: #1ac0ea;
    padding: 0.5rem;
  }
}

.contact {
  display: flex;
  justify-content: space-between;
  padding: 0 1.6rem;

  .logo {
    width: 20rem;
    margin: 1rem;
  }

  .icon {
    width: 3.5rem;
    margin: 0.5rem;
  }
}

.subfooter {
  padding: 1.5rem 2rem;
  //text-align: justify;
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  font-size: 0.9rem;
}


