$paddingY: 5rem;
$paddingX: 3rem;

.item {
  padding: $paddingY $paddingX $paddingY 0;
  background-size: cover;
  color: white;
  flex-direction: column;
  justify-content: space-between;

  .title {
    border-bottom: 3px solid #1ac0ea;
    padding-left: $paddingX;
    font-family: "Akrobat Bold", sans-serif;
    font-size: 2rem;
  }

  .text {
    padding: 1.5rem 0 1.5rem $paddingX;
    font-family: "Open Sans", sans-serif;
    font-weight: 300;
    font-size: 1.1rem;
  }

  .button {
    margin-left: $paddingX;
    width: 12rem;
  }
}

.item.fund {
  background-image: url("images/fund.png");
}
.item.portfel {
  background-image: url("images/portfel.png");
}
