.header {
  background-image: url("images/bg.png");
}

.container {
  font-family: "Open Sans", sans-serif;
  flex-direction: column;
  align-items: center;
  padding: 0 1rem 2rem 1rem;
  margin: auto;
}

.subheader {
  max-width: 750px;
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  font-size: 1.6rem;
  text-align: center;
  padding: 1rem 0;
}

.advantageItem {
  $paddingX: 1rem;
  $paddingY: 2rem;

  flex-direction: column;
  padding: $paddingX $paddingY;

  .subsubheader {
    font-family: "Raleway", sans-serif;
    font-weight: 700;
    font-size: 1.1rem;
  }

  .icon {
    position: absolute;
    width: 7rem;
    z-index: -1;
    top: -$paddingX;
    left: -$paddingY;
  }
}

.divider {
  background-color: #1ac0ea;
  height: 2px;
  width: 35%;
  margin: 1rem 0;
}

.openAccount {
  color: #1ac0ea;
  font-family: "Raleway", sans-serif;
  font-weight: 800;
  font-size: 1.6rem;
}
