.header {
  background-image: url("images/bg.png");
}

.container {
  font-family: "Open Sans", sans-serif;
  flex-direction: column;
  align-items: center;
  padding: 0 1rem 2rem 1rem;
  margin: auto;
}

.brief {
  padding: 1rem 0;
  text-align: justify;
  font-size: 1.1rem;
}

.subheader {
  max-width: 750px;
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  font-size: 1.6rem;
  text-align: center;
  padding: 1rem 0;
}

.numberItem {
  $padding: 2rem;

  flex-direction: column;
  justify-content: center;
  padding: $padding;
  margin: 0.5rem 0;

  .number {
    position: absolute;
    top: 0;
    left: 0;
    margin-left: -$padding/2;
    margin-top: -$padding/2;
    font-weight: 900;
    font-size: 10rem;
    line-height: 1;
    color: #ededed;
    z-index: -1;
  }

  .title {
    font-family: "Raleway", sans-serif;
    font-weight: 700;
  }

  .text {
    font-size: 0.9rem;
  }
}

.divider {
  background-color: #1ac0ea;
  height: 2px;
  width: 35%;
  margin: 1rem 0;
}
