.row {
  justify-content: center;
  height: unset;
}

.container {
  width: 100%;
  color: white;
  font-family: 'Open Sans', sans-serif;
  font-size: 1.1rem;
  font-weight: 300;
}

.tabs {
  display: flex;
  margin-bottom: 0.7rem;
}

.tab {
  width: 50%;
  text-align: center;
  user-select: none;
  font-size: 1.1rem;
}

.tab.inactive {
  color: #1ac0ea;
  text-decoration: underline;
}

.input {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0.15rem solid white;
  height: 3rem;
  margin-bottom: 0.7rem;


  .icon {
    flex: 0;
    min-width: 3rem;
    display: flex;
    justify-content: center;
  }

  .inputElementContainer {
    flex: 1;

    .element {
      border: none;
      color: white;
      background-color: transparent;
      width: 100%;
      outline: none;
      font-size: 1.2rem;
      font-family: 'Open Sans', sans-serif;
      font-weight: 300;
    }

    .element::placeholder {
      color: white;
      font-size: 1.2rem;
    }

  }
}

.button {
  border-width: 0.15rem;
  height: 3rem;
  margin-top: 0.7rem;
}

.registerSuccess {
  padding: 1rem;
  text-align: center;
  width: 100%;
  color: white;
  font-family: 'Open Sans', sans-serif;
  font-size: 1.5rem;
  font-weight: 400;
}

.continue {
  width: 20rem;
  padding-top: 2rem;
  margin: auto;
}

.email {
  font-weight: bold;
}

.fail {
  width: 100%;
  white-space: nowrap;
  justify-content: center;
  font-family: 'Montserrat', sans-serif;
  align-items: center;
  font-size: 1.2rem;
  height: 0;
  display: flex;
  font-weight: 700;
  color: white;
  opacity: 0;
  transition:  0.2s ease-in-out;
}

.fail.showing {
  opacity: 1;
  height: 2.5rem;
}

.remember {
  font-family: 'Montserrat', sans-serif;
  display: flex;
  align-items: center;
  font-size: 0.8rem;
  font-weight: 300;

    .checkbox {
      margin-right: 0.5rem;
    }
}

.forgot {
  font-family: 'Montserrat', sans-serif;
  display: flex;
  font-size: 0.8rem;
  font-weight: 300;
  justify-content: flex-end;
}

.forgot:hover {
  text-decoration: underline;
}

.forgotModal {
  padding: 1rem 2rem;
  font-family: 'Montserrat', sans-serif;
  font-size: 1.3rem;

  .resetFail {
    height: 1rem;
  }

  .resetFail.showing {
    height: 3rem;
  }

  .success {
    text-align: center;
    white-space: pre-wrap;

    .successEmail {
      font-weight: 700;
      font-size: 1.5rem;
    }
  }

  .please {
    font-size: 1.4rem;
  }

  .box {
    background-color: white;
    color: black;
    border-radius: 0.2rem;
    overflow: hidden;
    margin-top: 0.5rem;
    height: 2.75rem;

    .boxInput {
      border: none;
      outline: none;
      width: 100%;
      height: 100%;
      font-weight: 500;
      padding: 0 0.5rem;
    }
  }
}

.password {
  ///////////?
}

.viewer {
  background-color: white;
  //padding: 1rem 0;
  border-radius: 1rem;
  overflow: hidden;

  > div {
    overflow: auto;
  }

  ::-webkit-scrollbar {
    padding: 1rem 0;
    position: absolute;
    background-color: transparent;
    width: 6px;
    height: 6px;
  }

  ::-webkit-scrollbar-thumb {
    background-color: rgb(173, 174, 177);
    border-radius: 1rem;
    margin-right: 5px;
  }
}

.check {
  display: flex;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  justify-content: flex-start;
  align-items: center;
  padding: 0.2rem 0;

  .text {
    flex: 1;
    //display: flex;
    padding: 0 0.5rem;
    font-size: 0.85rem;

    .link {
      color: #1ac0ea;
      text-decoration: underline;
    }
  }
}
