.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  //background-color: #42287D;
  background-color: #3b509f;
  color: white;
  min-width: 16rem;
  width: 16rem;
  user-select: none;
  padding: 1rem 0;
  font-weight: 400;
  //font-family: "Raleway", sans-serif;
  font-family: "Open Sans", sans-serif;
}

.menu {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}

.user {
  padding: 1rem 0;
  text-align: center;

  .name {
    padding-top: 0.5rem;
  }
}

.tab {
  font-size: 0.8rem;
  display: flex;
  align-items: center;
  height: 3.5rem;
  width: 100%;
  flex-shrink: 0;

  .iconContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 3rem;
    margin-left: 0.75rem;
  }

  //transition: background-color 0.1s ease-in-out;
  padding: 0 2rem 0 4px;
}

.tab.logo {
  //padding: 0 2.5rem;
  //height: 4rem;
  //width: 6rem;
}

.logo {
  width: 10rem;
}

.tab.active {
  //background-color: #4C2D90;
  background-color: #2d4293;
  border-left: 4px solid #37c4e8;
  padding-left: 0;
}

.avatarContainer {
  overflow: hidden;
  border-radius: 100%;
  position: relative;
  margin: auto;
  //text-align: center;
  //display: flex;
  //justify-content: center;
  //margin-left: 15px;
}

.avatarPlaceholder {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #F0F0F0;
}

.avatarPlaceholderIcon {
  color: #D2D2D2;
}

.logout {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  padding: 0 2rem;
}
