.container {
  flex: 1;
  display: flex;
  justify-content: flex-end;
}

.dropdown {
  position: fixed;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 0;
  overflow: hidden;
  transition: 0.2s ease-in-out;
  padding: 0.5rem 0;
  border-radius: 0.5rem;

  $shadowAlpha: 0.3;
  $shadowBlur: 6px;

  -webkit-box-shadow: 0px 0px $shadowBlur 0px rgba(0,0,0,$shadowAlpha);
  -moz-box-shadow: 0px 0px $shadowBlur 0px rgba(0,0,0,$shadowAlpha);
  box-shadow: 0px 0px $shadowBlur 0px rgba(0,0,0,$shadowAlpha);
}

.dropdown.showing {
  opacity: 1;
}

.dropItem {
  font-family: 'Montserrat', sans-serif;
  font-size: 0.9rem;
  font-weight: 400;
  width: 100%;
  padding: 0.5rem 0.75rem;
  transition: 0.1s ease-in-out;
}

.dropItem:hover {
  background-color: rgba(26, 192, 234, 0.18);
  cursor: pointer;
}
