.container {
  width: 100%;
  background-image: url("images/bg.png");
  background-size: cover;
  display: flex;
  justify-content: center;
  color: white;

  .mainTitle {
    font-family: "Raleway", sans-serif;
    font-size: 2rem;
    font-weight: 700;
    margin-top: 0.5rem;
    text-align: center;
  }

  .item {
    flex-direction: column;
    padding: 1rem;

    .icon {
      width: 4rem;
      margin: 0.5rem 0;
    }

    .title {
      font-family: 'Akrobat Bold', sans-serif;
      font-size: 1.5rem;
    }

    .text {
      font-family: 'Open Sans', sans-serif;
      font-weight: 300;
      font-size: 1rem;
    }
  }
}
