.header {
  background-image: url("../Main/components/Banner/images/bg3.png");
}

.formContainer {
  margin: auto;
  padding: 1rem 0 2rem 0;
}

.title {
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  text-align: center;
  padding-top: 1rem;
  font-size: 2rem;
}

.form {
  padding: 1rem;
  background-color: white;
  -webkit-box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.3);
  -moz-box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.3);
  box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.3);

  .button {
    background-color: #1ac0ea;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0.25rem 0.25rem 0.25rem 0;
    width: 12rem;
  }

  .input {
    border: 1px solid hsl(0,0%,80%);
    height: 2.5rem;
    padding: 0 18px;
  }

  .input.email {
    margin-right: 0;
    border-right: none;
  }

  .area {
    border: 1px solid hsl(0,0%,80%);
    resize: none;
    height: 10rem;
    padding: 8px 18px;

  }

  .area, .input {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 0.9rem;
    color: #333333;
    width: 100%;
    margin: 0.25rem;
    outline: none;
  }

  .input::placeholder, .area::placeholder {
    color: #333333;
  }
}

.modal {
  font-family: 'Montserrat', sans-serif;
  padding: 1rem 0.5rem;
  font-size: 1.2rem;
  text-align: center;
}
