.container {
  font-family: 'Montserrat', sans-serif;
  display: flex;
  flex-direction: column;
  padding: 0 0.5rem;
}

.status {
  font-size: 1.3rem;
  color: #878787;
  //color: #333;
  font-weight: 700;
  display: flex;
  align-items: center;
  margin-bottom: 0.25rem;

  .statusText {
    font-weight: 700;
    background-color: #aa2d0d;
    color: white;
    padding: 0.1rem 0.5rem;
    margin-left: 0.5rem;
    font-size: 1rem;
    border-radius: 0.25rem;
  }

  .statusText.verified {
    background-color: #00CF7E;
  }
}

.editable {
  .header {
    margin-top: 0.5rem;
    font-size: 1rem;
    color: #878787;
    font-weight: 700;
  }
}

.fields {
  margin-left: -0.5rem;
}

.inputHeader {
  font-size: 0.9rem;
  white-space: nowrap;
  color: #878787;
  font-weight: 700;
}

.inputContainer {
  display: flex;
  flex-direction: column;
  padding: 0.5rem;

  .inputElement {
    outline: none;
    border: none;
    border-radius: 0.2rem;
    //width: 20rem;
    height: 36px;
    //padding: 0 1rem;
    padding-left: 12px;
    //border: 1px solid rgba(153,153,153,0.5);
  }
}

.radioItem {
  display: flex;
  align-items: center;
  font-size: 1rem;
  padding: 0.15rem 0;

  .radio {
    margin-right: 0.5rem;
  }
}

.shadow {
  -webkit-box-shadow: 2px 2px 2px 0px rgba(0,0,0,0.15);
  -moz-box-shadow: 2px 2px 2px 0px rgba(0,0,0,0.15);
  box-shadow: 2px 2px 2px 0px rgba(0,0,0,0.15);
}

.select {
  border-radius: 0.2rem;
  border: none;
}

.buttonContainer {
  margin: 1.25rem 0;
  width: 160px;

  .button {
    border: none;
    border-radius: 0.2rem;
  }
}
