.container {
    background-color: rgba(0,0,0,0.25);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
    font-family: 'Montserrat', sans-serif;
}

.box {
    background-color: #3B509F;
    /*background-color: #fff;*/
    width: 600px;
    border-radius: 0.2rem;
    /*padding: 1rem 2rem;*/
    padding: 1rem 0;
}

.header {
    margin-bottom: 1rem;
    font-size: 1.5rem;
    font-weight: 800;
    color: white;
    /*font-family: "Raleway", sans-serif;*/
    text-align: left;
    border-bottom: 4px solid #1ac0ea;
    padding: 0 2rem 0.5rem 2rem;
}

.main {
    display: flex;
    justify-content: center;
    padding: 0 2rem;
}

.rightSideContainer {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-between;
}

.uploadBox {
    cursor: pointer;
    outline: none;
    border-radius: 0.2rem;
    padding: 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    /*height: 90px;*/
    width: 100%;
    flex: 1;
    border-style: dashed;
    /*border-color: rgba(204, 204, 204, 0.4);*/
    border-color: white;
    color: white;
    /*color: rgba(204, 204, 204, 0.9);*/
    text-align: center;
    border-width: 0.12rem;
    font-size: 1rem;
    transition: 0.15s ease-in-out;
}

.uploadBoxAccept {
    border-color: rgba(20, 192, 94, 0.85);
    color: rgba(20, 192, 94, 0.85);
}

.uploadBoxReject {
    border-color: rgba(170, 45, 13, 0.85);
    color: rgba(170, 45, 13, 0.85);
}

.avatarContainer {
    flex-shrink: 0;
    flex-grow: 0;
    margin-right: 1.5rem;
    width: 200px;
    height: 200px;
    border-radius: 100%;
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.avatarPlaceholder {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #F0F0F0;
}

.avatarPlaceholderIcon {
    color: #D2D2D2;
}

.fileName {
    padding: 0.5rem 1rem;
}

.buttonsContainer {
    display: flex;
    margin-top: 0.7rem;
}

.rightButton {
    margin-left: 0.5rem;
}

